.movie-resposta {
    width: 100%;
    height: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;

    .movie-head {
        width: 100%;
        min-height: 10%;
        text-align: center;
        border-bottom: solid black 3px;
    }

    .corpo-movie {
        width: 100%;
        height: 100%;
        display: flex;
        justify-content: center;
        .ul-body {
            width: 100%;
            height: 100%;
            display: flex;
            justify-content: center;
            align-items: center;
            flex-wrap: wrap;
            overflow: scroll;
            .li-card {
                max-height: 50%;
                max-width: 50%;
                display: flex;
                justify-content: center;
                box-sizing: border-box;
                .movie-ca {
                    width: 100%;
                    display: flex;
                    flex-direction: column;
                    align-items: center;
                    .movie-title {
                        font-size: 1rem;
                    }
                    //.movie-type{}
                    .movie-poster {
                        height: 280px;
                    }
                }
    
            }
    
    
        }
    }




}

.select-div {
    width: 50%;
    padding: 30px;
    a.movie-card.selected {
        max-width: 100%;
        max-height: 100%;
        display: flex;
        justify-content: center;
        align-items: center;
        flex-direction: column;

        .movie-poster {
            max-height: 80%;
            width: 100%;
        }

        .movie-title {
            font-size: 30pt;
        }
    }
}
