* {
  box-sizing: border-box;
  overflow: hidden;
}
@media (max-width: 1100px) {
  * {
    overflow: auto;
  }
}

.div-bod {
  width: 100%;
  display: flex;
  overflow: hidden;
}
.div-bod .esq-charts {
  width: 5vw;
}
.div-bod .esq-charts .ul-charts {
  height: 100%;
  display: flex;
  flex-direction: column;
  justify-content: space-around;
}
.div-bod .esq-charts .ul-charts li {
  height: auto;
}
.div-bod .esq-charts .ul-charts li a {
  height: auto;
  font-size: 34pt;
}
.div-bod .dir-charts {
  width: 95vw;
  padding: 15px;
  box-sizing: border-box;
}
@media (max-width: 1100px) {
  .div-bod .dir-charts {
    overflow: auto;
  }
}
.div-bod .dir-charts .text-charts {
  box-sizing: border-box;
  border-radius: 35px 35px 0px 0px;
  height: 50%;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  text-align: center;
}
.div-bod .dir-charts .text-charts .skills-btn {
  height: 15%;
  width: 100%;
  display: flex;
  justify-content: space-around;
  flex-direction: row;
}
@media (max-width: 1100px) {
  .div-bod .dir-charts .text-charts .skills-btn {
    flex-direction: column;
    height: auto;
  }
}
.div-bod .dir-charts .text-charts .skills-btn ul {
  width: 100%;
  display: flex;
}
@media (max-width: 1100px) {
  .div-bod .dir-charts .text-charts .skills-btn ul {
    flex-direction: column;
    height: auto;
  }
}
.div-bod .dir-charts .text-charts .skills-btn ul li {
  padding: 5px;
}
@media (max-width: 1100px) {
  .div-bod .dir-charts .text-charts .skills-btn ul li {
    overflow: hidden;
  }
}
.div-bod .dir-charts .text-charts .skills-btn ul li a {
  padding: 3px 20px;
  border-radius: 10px;
  color: #fff;
  background: black;
  font-size: 12pt;
  text-align: center;
}
@media (max-width: 1100px) {
  .div-bod .dir-charts .text-charts .skills-btn ul li a {
    margin: 20px;
    padding: 20px 20px;
    width: 100%;
    overflow: hidden;
  }
}
.div-bod .dir-charts .text-charts .txt-btn-resp {
  height: 85%;
}
.div-bod .dir-charts .real-charts {
  box-sizing: border-box;
  border-radius: 0px 0px 35px 35px;
  height: 50%;
  display: flex;
  padding: 5px;
  justify-content: center;
  align-items: center;
}
@media (max-width: 1100px) {
  .div-bod .dir-charts .real-charts {
    flex-direction: column;
    height: auto;
  }
}
.div-bod .dir-charts .real-charts .inside-render {
  width: 50%;
  box-sizing: border-box;
  padding: 0px;
}
@media (max-width: 1100px) {
  .div-bod .dir-charts .real-charts .inside-render {
    width: 100%;
  }
}
.div-bod .dir-charts .real-charts .inside-render .bar {
  width: 100%;
}
.div-bod .dir-charts .real-charts .txt-exp {
  height: 100%;
  width: 50%;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  padding: 25px;
}
@media (max-width: 1100px) {
  .div-bod .dir-charts .real-charts .txt-exp {
    width: 100%;
  }
}
.div-bod .dir-charts .real-charts .txt-exp h3 {
  width: 100%;
  text-align: center;
}

.hid {
  display: none;
}/*# sourceMappingURL=charts-tab.css.map */