*{
    box-sizing: border-box;
    overflow: hidden;
    @media(max-width: 1100px){
        overflow: auto;
    }
}

.div-bod{
    width: 100%;
    display: flex;
    overflow: hidden;
    .esq-charts{
        width: 5vw;
        .ul-charts{
            height: 100%;
            display: flex;
            flex-direction: column;
            justify-content: space-around;
            li{
                height: auto;
                a{
                    height: auto;
                    font-size: 34pt;
                }
            }
        }
    }
    .dir-charts{
        width: 95vw;
        padding: 15px;
        box-sizing: border-box;
        @media(max-width: 1100px){
            overflow: auto;
        }
        .text-charts{
            box-sizing: border-box;
            border-radius: 35px 35px 0px 0px;
            height: 50%;
            //background: #d6d6d6;
            display: flex;
            flex-direction: column;
            justify-content: center;
            align-items: center;
            text-align: center;
            .skills-btn{
                height: 15%;
                @media(max-width: 1100px){
                    flex-direction: column;
                    height: auto;
                }
                width: 100%;
                display: flex;
                justify-content: space-around;
                flex-direction: row;
                ul{
                    width: 100%;
                    display: flex;
                    @media(max-width: 1100px){
                            flex-direction: column;
                            height: auto;
                    }
                    li {
                        padding: 5px;
                        @media(max-width: 1100px){
                            overflow: hidden;
                        }
                        a {
                            padding: 3px 20px;
                            border-radius: 10px;
                            color: #fff;
                            background: black;
                            font-size: 12pt;
                            text-align: center;
                            @media(max-width: 1100px){
                                margin: 20px;
                                padding: 20px 20px;
                                width: 100%;
                                overflow: hidden;
                            }
                        }
                    }
                    }
            }
            .txt-btn-resp{
                
                height: 85%;
            }
            
        }
        .real-charts{
            box-sizing: border-box;
            border-radius: 0px 0px 35px 35px;
            height: 50%;
            //background: #d6d6d6;
            display: flex;
            padding: 5px;
            justify-content: center;
            align-items: center;
            @media(max-width: 1100px){
                flex-direction: column;
                height: auto;
            }
            .inside-render{
                width: 50%;
                box-sizing: border-box;
                padding: 0px;
                @media(max-width: 1100px){
                    width: 100%;
                }
                .bar{
                    width: 100%;
                }
            }
            .txt-exp{
                height: 100%;
                width: 50%;
                display: flex;
                flex-direction: column;
                justify-content: center;
                align-items: center;
                padding: 25px;
                @media(max-width: 1100px){
                    width: 100%;
                }
                h3{
                    width: 100%;
                    text-align: center;
                }
            }
        }
    }
}
.hid{
    display: none;
  }