.full-response{
    height: 100%;
    width: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
    flex-wrap: wrap;
}
.title-res{
    width: 100%;
    height: 5%;
    display: flex;
    justify-content: center;
    align-items: center;
}
input{
    height: 30px;
    width: 300px;
    border-radius: 15px;
    text-align: center;
    margin: 40px;
}
button{
    height: 30px;
    width: 100px;
    border-radius: 15px;
}

.body-response{
    height: 90%;
    width: 100%;
}