.movie-resposta {
  width: 100%;
  height: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
}
.movie-resposta .movie-head {
  width: 100%;
  min-height: 10%;
  text-align: center;
  border-bottom: solid black 3px;
}
.movie-resposta .corpo-movie {
  width: 100%;
  height: 100%;
  display: flex;
  justify-content: center;
}
.movie-resposta .corpo-movie .ul-body {
  width: 100%;
  height: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-wrap: wrap;
  overflow: scroll;
}
.movie-resposta .corpo-movie .ul-body .li-card {
  max-height: 50%;
  max-width: 50%;
  display: flex;
  justify-content: center;
  box-sizing: border-box;
}
.movie-resposta .corpo-movie .ul-body .li-card .movie-ca {
  width: 100%;
  display: flex;
  flex-direction: column;
  align-items: center;
}
.movie-resposta .corpo-movie .ul-body .li-card .movie-ca .movie-title {
  font-size: 1rem;
}
.movie-resposta .corpo-movie .ul-body .li-card .movie-ca .movie-poster {
  height: 280px;
}

.select-div {
  width: 50%;
  padding: 30px;
}
.select-div a.movie-card.selected {
  max-width: 100%;
  max-height: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
}
.select-div a.movie-card.selected .movie-poster {
  max-height: 80%;
  width: 100%;
}
.select-div a.movie-card.selected .movie-title {
  font-size: 30pt;
}/*# sourceMappingURL=movie-list.css.map */